aside {
	position: fixed;
	height: 100vh;
	width: 200px;
	top: 0;
	left: -18px;
	background: #F9F9F9;
	z-index: 2;
	transform: skewX(6deg);

    .wrapper {
    	transform: skew(-6deg);
    	position: absolute;
    	left: 53px;
    }
}

aside::before {
	content: '';
	background: #D8D8D8;
	height: 100vh;
	width: 228px;
	position: absolute;
	top: 0;
	left: -61px;
	z-index: 0;
	transform: skew(-18deg);
	border-right: 1px solid #755F5F;
}

ul.nav {
	position:fixed;
	left:15px;
	top: 200px;
	padding: 10px;
	width: 150px;
	font-family: $navFont;
	text-transform: uppercase;
	z-index: 3;
	text-align: left;
	font-size: 14px;
	line-height: 40px;
	margin: 0;
	padding: 0;

	li {
		display:block;
		margin:0;
		padding:0;
	}
}

ul.nav li a {
	text-decoration: none;
	margin: 0;
	font-size: 30px;
	letter-spacing: 3px;
	color: $navLinkColour;

	&:hover {
		color: $accent;
	}
	&.active {
		color: $accent;	
	}
}


